a.btn.sign-btn{
    font-family: 'Jost', sans-serif;
    border-image-slice: 1;
    border: 2px solid transparent;
    background: -webkit-linear-gradient(rgba(56, 57, 111, 1), rgba(210, 27, 48, 1));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    border-image: -webkit-linear-gradient(rgba(56, 57, 111, 1), rgba(210, 27, 48, 1)) 10% round;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 29px;
    padding: 8px 40px;
   
   
}
.clock-btn{
    font-family: 'Jost', sans-serif;
  border:2px solid rgba(56, 57, 111, 1);
    background: -webkit-linear-gradient(rgba(56, 57, 111, 1), rgba(210, 27, 48, 1));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    border-image: -webkit-linear-gradient(rgba(56, 57, 111, 1), rgba(210, 27, 48, 1)) 20% round;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 29px;
    padding: 8px 40px;  
}

a.btn.sign-btn:hover,.clock-btn:hover{
    background: -webkit-linear-gradient(rgba(56, 57, 111, 1), rgba(210, 27, 48, 1));
    color:#FFFFFF; 
  border-image: none;
    -webkit-background-clip: unset;
    -webkit-text-fill-color:#FFFFFF;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.bell{
    height: 50px;
    width: 50px;
    border-radius: 50%;
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 1);
	-webkit-transform: scale(1);
	        transform: scale(1);
	-webkit-animation: pulse 5s infinite;
	        animation: pulse 5s infinite;
}
@-webkit-keyframes pulse {
	0% {
		-webkit-transform: scale(0.95);
		        transform: scale(0.95);
		box-shadow: 0 0 0 0 rgba(210, 27, 48, 0.7);
	}

	70% {
		-webkit-transform: scale(1);
		        transform: scale(1);
		box-shadow: 0 0 0 10px rgba(210, 27, 48, 0);
	}

	100% {
		-webkit-transform: scale(0.95);
		        transform: scale(0.95);
		box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
	}
}
@keyframes pulse {
	0% {
		-webkit-transform: scale(0.95);
		        transform: scale(0.95);
		box-shadow: 0 0 0 0 rgba(210, 27, 48, 0.7);
	}

	70% {
		-webkit-transform: scale(1);
		        transform: scale(1);
		box-shadow: 0 0 0 10px rgba(210, 27, 48, 0);
	}

	100% {
		-webkit-transform: scale(0.95);
		        transform: scale(0.95);
		box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
	}
}
